p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  font-family: Proxima Nova;
}

monti-container{
  width: 100%;
  display: block;
}

.user-container{
  width: 100%;
  display: block;
  text-align: right;
}

.user{
  max-width: 240px;
  height: auto;
  display: inline-block;
  text-align: left;
  margin: 4px 24px 4px auto;
  padding: 10px 16px 9px 12px;
  background-color: #00e9ba;
  -webkit-box-shadow: 0 5px 10px 0 rgba(0, 233, 186, 0.32);
          box-shadow: 0 5px 10px 0 rgba(0, 233, 186, 0.32);
  border-radius: 20px 5px 5px 20px;
  -webkit-animation: 0.3s ease-out 0s 1 userBubbleAnimation;
          animation: 0.3s ease-out 0s 1 userBubbleAnimation;
  color: #ffffff;
}

.user-first{
  border-radius: 20px 20px 5px 20px;
}
.user-last{
  border-radius: 20px 5px 20px 20px;
}

.monti{
  max-width: 240px;
  height: auto;
  display: inline-block;

  margin: 4px auto 4px 24px; /*Lav plads til Monti*/
  padding: 9px 12px 10px 16px;
  background-color: #ffffff;
  -webkit-box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.05);
          box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.05);
  border-radius: 5px 20px 20px 5px;

  -webkit-animation: 0.3s ease-out 0s 1 montiBubbleAnimation;

          animation: 0.3s ease-out 0s 1 montiBubbleAnimation;

  color: #262b3d;
}

.monti-first{
  border-radius: 20px 20px 20px 5px;
}
.monti-last{
  border-radius: 5px 20px 20px 20px;
}

.hidden {
  visibility: hidden;
}

@-webkit-keyframes userBubbleAnimation {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  90% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes userBubbleAnimation {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  90% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes montiBubbleAnimation {
  0% {
    -webkit-transform: translateX(-30%) scale(0.1);
            transform: translateX(-30%) scale(0.1);
  }
  90% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
}

@keyframes montiBubbleAnimation {
  0% {
    -webkit-transform: translateX(-30%) scale(0.1);
            transform: translateX(-30%) scale(0.1);
  }
  90% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
}



