.action-area {
    position: fixed;
    bottom: 0px;
    right: 0px;
    width: 100%;
    border-radius: 20px 20px 0px 0px;
    background-color: white;
    -webkit-box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12);
    padding-top: 24px;
    height: 33%;
}


