.slider-block {
    /* border-radius: 20px;
    background-color: white;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12);
    padding-top: 24px; */
    -webkit-animation: 0.4s ease-out 0s 1 sliderShow;
            animation: 0.4s ease-out 0s 1 sliderShow;
}

.slider-container {
    height: 43px;
    text-align: center;
    width: 100%;
    background-color: #00e9ba;
}

.slider-wrapper {
    margin: 0 auto;
    width: 75%;
    position: relative;
}

.rheostat {
    height: 30px;
}

.below-slider {
    /* background-color: #3422EE; */
    /* background-color: red; */
    width: 85px;
    height: 58px;
    position: absolute;
    top: 0;
    margin-top: -15px;
    margin-left: -42.5px;
    /* border-radius: 30px; */
    z-index: 1;
}

.bullet {
    background-color: green;
    width: 15px;
    height: 15px;
    border-radius: 15px;
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    margin-top: 0px;
    margin-left: -10px;
}

.bullet-move {
    margin-right: auto;
    margin-top: -100px;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.bullet-expanded {
    margin-left: -40px;
    margin-top: -115px;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    width: 80px;
    height: 50px;
    -webkit-transform-origin: center;
        -ms-transform-origin: center;
            transform-origin: center;
}

.rheostat-handle {
    width: 41px;
    height: 41px;
    background-color: transparent;
    border-radius: 30px;
    /* border: solid red 1px; */
    /* box-shadow: 0px 1px 4px 2px rgba(0, 0, 0, 0.3); */
    border: none;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-left: -20.5px;
    z-index: 1000;
    margin-top: -1px;
}

.rhostat-background {
    height: 1px;
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.battery-wrapper {
    height: 84px;
    width: 170px;
    margin: 0 auto;
    padding-top: 22px;
    padding-bottom: 37px;
}

.battery {
    border: 3px solid grey;
    width: 30px;
    height: 60px;
    margin-right: 1px;
    margin-left: 1px;
    display: inline-block;
}

.battery-full {
    background-color: green;
}

.battery-nob {
    width: 10px;
    height: 20px;
    background-color: grey;
    display: inline-block;
    margin-bottom: 25px;
}

.pits {
    position: absolute;
    width: 100%;
}

.pit {
    width: 3px;
    height: 6px;
    border-radius: 2px;
    background-color: white;
    display: inline-block;
    position: absolute;
    top: -10px;
    /* z-index: -1; */
}

@-webkit-keyframes sliderShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes sliderShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



