.button {
  max-width: 80vw;
  -webkit-box-shadow: 0 5px 13px 0 rgba(0, 233, 186, 0.56);
          box-shadow: 0 5px 13px 0 rgba(0, 233, 186, 0.56);
  padding: 10px 20px;
  cursor: pointer;
  text-align: left;
  border: 0;
  outline: 0;
  border-radius: 20px;
  margin: 0 auto;
  background-color: #00e9ba;
  font-family: Proxima Nova;
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;

  -webkit-animation: 0.3s ease-out 0s 1 buttonShow;

          animation: 0.3s ease-out 0s 1 buttonShow;
}

.button:active {
  opacity: 0.6;
  -webkit-transform: scale(0.97);
      -ms-transform: scale(0.97);
          transform: scale(0.97);
}

.icon {
  width: 24px;
  height: 24px;
  margin: -3px -14px -10px 10px;
  -o-object-fit: contain;
     object-fit: contain;
  float: right;
}

@-webkit-keyframes buttonShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes buttonShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



